/* src/style.css */

/* Global styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

/* Hero section */
.hero {
  background-image: url("C:\Users\Jon\OneDrive\Desktop\grupo-sabrosura\src\Backgroundtest.jpg.jpg");
  background-size: cover;
  background-position: center;
  padding: 100px 0;
  color: white;
  text-align: center;
}

.hero h1 {
  font-size: 3rem;
  font-weight: bold;
}

/* Navbar styles */
nav {
  background-color: #003893; /* Colombian flag blue */
  color: white;
  padding: 15px 0;
}

nav a {
  color: white;
  padding: 10px;
  text-decoration: none;
}

nav a:hover {
  color: #FCD116; /* Colombian flag yellow */
}
